.main-container {
  @include center;
}

.main-container__submit {
  width: 320px;
  padding: 0 20px;
  box-sizing: border-box;
  margin: 0 auto;
}

.main-container__button {
  text-transform: uppercase;
}

@media (min-width: $tablet-width) {
  .main-container__submit {
    display: flex;
    gap: 64px;
    width: 768px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    margin-bottom: 72px;
  }

  .main-container__button {
    font-size: 20px;
    text-transform: uppercase;
  }

  .main-container__legend {
    font-size: 16px;
    line-height: 16px;
  }
}

@media (min-width: $desktop-width) {
  .main-container__submit {
    display: flex;
    gap: 84px;
    width: 1330px;
    padding: 0 55px;
    box-sizing: border-box;
    margin: 0 auto;
    margin-bottom: 82px;
  }
}
