.main-header {
  width: 100%;
  position: relative;
}

.main-header__wrapper {
  margin: 0 auto;
  width: 320px;
  padding: 0 20px;
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-auto-rows: minmax(24px, auto);
  gap: 60px;
  min-height: 66px;
}

.main-header__toggle {
  display: block;
  width: 24px;
  height: 16px;
  background-color: #ffffff;
  border: none;
  position: absolute;
  cursor: pointer;
  top: 24px;
  z-index: 2;
  padding: 0;
}

.main-header__caption {
  width: 101px;
  height: 18px;
  padding-top: 23px;
}

.main-header__icon {
  width: 33px;
  height: 37px;
  padding-top: 10px;
}

.main-header__link {
  position: relative;
  cursor: pointer;
}

.main-header__link:hover {
  opacity: 0.8;
}

.main-header__link:active {
  opacity: 0.3;
}

.main-header__link::after {
  content: url(../img/caption.svg);
  width: 101px;
  height: 18px;
  position: absolute;
  top: 10px;
  left: 77px;
}

@media (min-width: 768px) {
  .main-header__wrapper {
    width: 768px;
    padding: 0 30px;
    box-sizing: border-box;
    display: flex;
    min-height: 52px;
    justify-content: end;
  }

  .main-header__link::after {
    display: none;
  }

  .main-header__toggle {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
    display: none;
  }

  .main-header__logo {
    width: 174px;
    height: 52px;
  }
}
@media (min-width: 1330px) {
  .main-header__wrapper {
    width: 1330px;
    padding: 0 55px;
    box-sizing: border-box;
    display: grid;
    grid-template-columns: 202px 2fr;
    gap: 538px;
    min-height: 140px;
  }

  .main-header__logo {
    width: 202px;
    height: 59px;
    padding-top: 53px;
  }

  .main-header__toggle {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
